/* Style pour desktop */
.card {
  padding: 0.5em;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1%;
}
.text-container{
  flex-grow: 1;
  text-align: left;
  line-height: 1em;

}
.title-card{
  color: #34495e;
  font-weight: lighter;
}

.img_desc {
  border-radius: 5px;
  max-width: 500px;
  width: 80%;
}
.img-container{
width: 25%;
display: flex;
justify-content: left;
}
.center{
  justify-content: center;
  align-items: center;
}

.pad-15{padding: 1.5em;}


